import { Fetch } from '@lastpass/http'
import { UnkownServiceError, createResponse, ResponseTypes } from '../responses'
import { SMSData } from '@lastpass/types/multifactor'

export const SUCCESS = 'success'

const success = () => createResponse(SUCCESS)

export const Responses = {
  success
}

export type Responses = ResponseTypes<typeof Responses>

export function create(fetch: Fetch) {
  return async function service(data: SMSData) {
    const [response, status] = await fetch.legacyXML('send_sms_passcodes.php', {
      smshash: data.hash,
      smstime: data.time,
      smsuid: data.uid
    })
    if (status === 200 && response.response.ok === '') {
      return success()
    }
    throw new UnkownServiceError()
  }
}
