import { take, call, put } from 'redux-saga/effects'
import * as MultifactorActions from '@lastpass/state/multifactor/actions'
import {
  MultifactorChallengeResponse,
  MultifactorChallenge
} from '@lastpass/types/multifactor'
import { SagaIterator } from '@redux-saga/core'
import { cancel, success, MultifactorRequestResult } from './phases'

export function createManualSaga(
  requestSaga: (data: MultifactorChallengeResponse) => SagaIterator
) {
  return function*(challenge: MultifactorChallenge) {
    while (true) {
      try {
        const action: MultifactorActions.Actions = yield take([
          MultifactorActions.SUBMIT_TOTP,
          MultifactorActions.CANCEL
        ])
        if (action.type === MultifactorActions.SUBMIT_TOTP) {
          const challengeResponse: MultifactorChallengeResponse = {
            challenge,
            trustedDeviceLabel: action.payload.label,
            totp: action.payload.totp
          }
          const requestResult: MultifactorRequestResult = yield call(
            requestSaga,
            challengeResponse
          )
          if (requestResult.succeeded) {
            return success(
              requestResult.trustedDeviceId,
              challengeResponse.trustedDeviceLabel
            )
          }
        } else {
          return cancel()
        }
      } catch (e) {
        yield put(MultifactorActions.error(e.toString()))
      }
    }
  }
}

export type ManualSaga = ReturnType<typeof createManualSaga>
