import { Fetch } from '@lastpass/http'
import { UnkownServiceError, createResponse, ResponseTypes } from '../responses'

export const SUCCESS = 'success'

const success = (iterations: number) => createResponse(SUCCESS, iterations)

export const Responses = {
  success
}

export type Responses = ResponseTypes<typeof Responses>

export function create(fetch: Fetch) {
  return async function service(username: string) {
    const [response, status] = await fetch.legacy('iterations.php', {
      email: username
    })
    if (status === 200 && response) {
      const iterations = parseInt(response)
      if (iterations !== NaN) {
        return success(iterations)
      }
    }
    throw new UnkownServiceError()
  }
}
