import { Fetch } from '@lastpass/http'
import * as LoginAPI from './login'
import * as IterationsAPI from './iterations'
import { LoginRequestService } from '@lastpass/services/login/request'

export const createLoginServices = (
  fetch: Fetch,
  requestService: LoginRequestService
) => {
  return {
    login: LoginAPI.create(fetch, requestService),
    iterations: IterationsAPI.create(fetch)
  }
}

export { LoginAPI, IterationsAPI }

export type Services = ReturnType<typeof createLoginServices>
