import { pbkdf2 } from '@lastpass/encryption/pbkdf2'

/**
 * Generates the login hash from the vault encryption key and master password
 * @param key
 * @param password
 */
export async function deriveHash(
  key: Buffer,
  password: string
): Promise<string> {
  const loginHash = await pbkdf2(key, password, 1, 32, 'sha256')
  return loginHash.toString('hex')
}
