import { take, race, call, put } from 'redux-saga/effects'
import * as MultifactorActions from '@lastpass/state/multifactor/actions'
import {
  MultifactorChallengeResponse,
  MultifactorChallenge
} from '@lastpass/types/multifactor'
import { SagaIterator } from 'redux-saga'
import { cancel, success, error, MultifactorRequestResult } from './phases'
import { MultifactorSagaState } from './saga-state'
import * as MultifactorServices from '@lastpass/server/multifactor'

export function createOutOfBandSaga(
  services: MultifactorServices.Services,
  requestSaga: (data: MultifactorChallengeResponse) => SagaIterator
) {
  return function*(
    challenge: MultifactorChallenge,
    state: MultifactorSagaState = {}
  ) {
    let challengeResponse: MultifactorChallengeResponse = { challenge }
    let retryId: string
    const retry = () => {
      challengeResponse = {
        challenge,
        // If the user entered a label we might as well send it in the auth request to save a later request to trust.php
        trustedDeviceLabel: state.trustedDeviceLabel,
        retryId
      }
    }
    while (true) {
      try {
        // Executes the request saga and cancels the request if the user
        // performs any action requiring an updated request or cancelling the multifactor
        // phase
        const [requestResult, action]: [
          MultifactorRequestResult,
          MultifactorActions.Actions
        ] = yield race([
          call(requestSaga, challengeResponse),
          take([MultifactorActions.SUBMIT_TOTP, MultifactorActions.CANCEL])
        ])
        if (requestResult) {
          if (requestResult.succeeded) {
            /**
             * If the multifactor response did not include a trusted device id we
             * need to manually call trust.php to send the label information
             */
            if (!requestResult.trustedDeviceId && state.trustedDeviceLabel) {
              const trustResult: MultifactorServices.TrustedDeviceAPI.Responses = yield call(
                services.trustedDevice,
                state.trustedDeviceLabel
              )
              if (
                trustResult.type ===
                MultifactorServices.TrustedDeviceAPI.SUCCESS
              ) {
                return success(
                  trustResult.body.trustedDeviceId,
                  state.trustedDeviceLabel
                )
              }
            }
            return success(
              requestResult.trustedDeviceId,
              challengeResponse.trustedDeviceLabel
            )
          } else if (requestResult.retryId) {
            retryId = requestResult.retryId
            retry()
          }
        } else if (action.type === MultifactorActions.CANCEL) {
          return cancel()
        } else if (action.type === MultifactorActions.SUBMIT_TOTP) {
          challengeResponse = {
            challenge,
            trustedDeviceLabel: action.payload.label,
            totp: action.payload.totp
          }
        }
      } catch (e) {
        // If the user tried to manually submit the TOTP but it failed start sending out of band retries again
        if (challengeResponse.totp) {
          yield put(MultifactorActions.error(e.toString()))
          retry()
        } else {
          // Otherwise if outofband returned an error stop polling and return the error
          return error(e.toString())
        }
      }
    }
  }
}

export type OutOfBandSaga = ReturnType<typeof createOutOfBandSaga>
