import { select, call, put } from 'redux-saga/effects'
import { SavedUsernamesRepository } from '@lastpass/repositories/login/saved-usernames'
import { LoginState } from '@lastpass/state/login/state'
import * as LoginActions from '@lastpass/state/login/actions'

export function createAddSavedUsernameSaga<T extends { login: LoginState }>() {
  return function*(username: string) {
    const usernames: string[] = yield select((state: T) => {
      return state.login.savedUsernames
    })
    // If username is already saved move it to the front, otherwise just insert at front
    const updatedUsernames = [
      username,
      ...usernames.filter(name => name !== username)
    ]
    yield call(SavedUsernamesRepository.set, updatedUsernames)
    yield put(LoginActions.setUsernames(updatedUsernames))
  }
}

export type AddSavedUsernameSaga = ReturnType<typeof createAddSavedUsernameSaga>
