import { takeEvery } from 'redux-saga/effects'
import { LoginState } from '@lastpass/state/login/state'
import * as LoginActions from '@lastpass/state/login/actions'
import * as LoginServices from '@lastpass/server/login'
import * as MultifactorServices from '@lastpass/server/multifactor'
import { initializeLoginSaga } from './initialize'
import { createIterationsSaga } from './iterations'
import { createLoginServerSaga } from './server'
import { createRemoveSavedUsernameSaga } from './remove-saved-username'
import { createAddSavedUsernameSaga } from './add-saved-username'
import { createSuccessSaga } from './success'
import { createLoginSaga } from './login'

export function createLoginFeatureSaga<T extends { login: LoginState }>(
  loginServices: LoginServices.Services,
  multifactorServices: MultifactorServices.Services
) {
  const removeSavedUsernameSaga = createRemoveSavedUsernameSaga<T>()
  const addSavedUsernameSaga = createAddSavedUsernameSaga<T>()
  const iterationsSaga = createIterationsSaga(loginServices)
  const successSaga = createSuccessSaga(addSavedUsernameSaga)
  const loginServerSaga = createLoginServerSaga(loginServices, successSaga)
  const loginSaga = createLoginSaga(
    iterationsSaga,
    loginServerSaga,
    multifactorServices
  )
  return function*() {
    yield takeEvery(LoginActions.INITIALIZE, initializeLoginSaga)
    yield takeEvery(LoginActions.LOGIN, loginSaga)
    yield takeEvery(LoginActions.REMOVEUSERNAME, removeSavedUsernameSaga)
  }
}
