import { Fetch } from '@lastpass/http'
import {
  UnkownServiceError,
  ServiceError,
  createResponse,
  ResponseTypes
} from '../responses'
import {
  MultifactorChallenge,
  MultifactorProvider,
  DUOWebSDK,
  MultifactorChallengeResponse
} from '@lastpass/types/multifactor'
import {
  LoginRequestService,
  LoginData
} from '@lastpass/services/login/request'
import { parseXml } from '@lastpass/http/helpers'

export const SUCCESS = 'success'
export const WRONG_ITERATIONS = 'iterations'
export const REQUIREPWCHANGE = 'requirepwchange'
export const MULTIFACTOR_CHALLENGE = 'multifactor'
export const MULTIFACTOR_RETRY_RESPONSE = 'multifactorretry'
export const WRONG_SERVER = 'wrongserver'

const success = (
  uid: number,
  csrf: string,
  suggestPasswordChange: boolean,
  legacyXML: string,
  upgradeIterations?: number,
  trustedDeviceId?: string
) =>
  createResponse(SUCCESS, {
    uid,
    csrf,
    suggestPasswordChange,
    upgradeIterations,
    trustedDeviceId,
    legacyXML
  })
const requirePasswordChange = () => createResponse(REQUIREPWCHANGE)
const wrongIterations = (iterations: number) =>
  createResponse(WRONG_ITERATIONS, { iterations })
const multifactorChallenge = (data: MultifactorChallenge) =>
  createResponse(MULTIFACTOR_CHALLENGE, data)
const multifactorRetryResponse = (retryId: string) =>
  createResponse(MULTIFACTOR_RETRY_RESPONSE, { retryId })

/**
 * If an EU users attempts logging into lastpass.com we get a
 * wrong server response and redirect on the web vault and retry
 * logging in with the extension
 * @param host the correct host to use
 */
const wrongServer = (host: string) => createResponse(WRONG_SERVER, { host })

export const Responses = {
  success,
  requirePasswordChange,
  wrongIterations,
  multifactorChallenge,
  multifactorRetryResponse,
  wrongServer
}

export type Responses = ResponseTypes<typeof Responses>

function getMultifactorProvider(
  /* eslint-disable-next-line */
  response: any
): MultifactorProvider | undefined {
  switch (response._cause) {
    case 'outofbandrequired':
      return response._outofbandtype
    case 'microsoftauthrequired':
      return MultifactorProvider.MICROSOFT_AUTHENTICATOR
    case 'googleauthrequired':
      return MultifactorProvider.GOOGLE_AUTHENTICATOR
    case 'gridresponserequired':
      return MultifactorProvider.GRID
    case 'multifactorresponserequired':
      return MultifactorProvider.FINGERPRINT
    case 'sesameotprequired':
      return MultifactorProvider.SESAME
    case 'otprequired':
      return MultifactorProvider.YUBIKEY
  }
}

/* eslint-disable-next-line */
function supports(response: any, capability: string) {
  if (response._capabilities) {
    return response._capabilities.split(',').includes(capability)
  }
  return false
}

/* eslint-disable-next-line */
function supportsVerificationCode(response: any) {
  if (supports(response, 'totp') || supports(response, 'passcode')) {
    return true
  }
  const provider = getMultifactorProvider(response)
  switch (provider) {
    case MultifactorProvider.MICROSOFT_AUTHENTICATOR:
    case MultifactorProvider.GOOGLE_AUTHENTICATOR:
    case MultifactorProvider.YUBIKEY:
      return true
  }
  return false
}

function getMultifactorChallenge(
  username: string,
  /* eslint-disable-next-line */
  response: any
): MultifactorChallenge | undefined {
  const provider = getMultifactorProvider(response)
  const duoWebSDK: DUOWebSDK | undefined =
    response._preferduowebsdk === '1'
      ? {
          host: response._duo_host,
          bytes: response._duo_bytes,
          signature: response._duo_signature
        }
      : undefined
  if (provider) {
    return {
      username,
      provider: {
        type: provider,
        image: response._outofbandimage,
        label: response._outofbandname,
        duoWebSDK
      },
      options: {
        outofbandauto: supports(response, 'outofbandauto'),
        outofband:
          supports(response, 'outofband') ||
          response._cause === 'outofbandrequired',
        verificationCode: supportsVerificationCode(response),
        disable: response._hidedisable === 'false',
        trust:
          (!response._allowtrust || response._allowtrust === '1') &&
          response._allowmultifactortrust === 'true'
      },
      sms: response._smshash && {
        uid: response._smsuid,
        hash: response._smshash,
        time: response._smstime
      },
      trustedDeviceLabel: response._trustlabel,
      trustExpired: response._trustexpired === '1'
    }
  }
}

const replaceHTML = function(error: string) {
  return error
    .replace(/&apos;/gi, "'")
    .replace(/&gt;/gi, '>')
    .replace(/&lt;/gi, '<')
    .replace(/&quot;/gi, '"')
    .replace(/&amp;/gi, '&')
}

export function create(fetch: Fetch, requestService: LoginRequestService) {
  /**
   * Performs the existing PBKDF2 based login process hitting legacy login.php endpoint
   * @param username
   * @param key
   * @param hash
   * @param iterations
   */
  return async function service(
    data: LoginData,
    challengeResponse?: MultifactorChallengeResponse,
    abortSignal?: AbortSignal
  ): Promise<Responses> {
    const requestData = await requestService(data, challengeResponse)
    const [responseText, status] = await fetch.legacy(
      'login.php',
      requestData,
      abortSignal
    )
    const response = parseXml(responseText)
    if (status === 200 && response) {
      const responseData = response.response ? response.response : response
      if (responseData && responseData.ok) {
        if (responseData.ok._pwresetreqd === '1') {
          return requirePasswordChange()
        }
        return success(
          responseData.ok._uid,
          responseData.ok._token,
          responseData.ok._oldpassword === '1',
          responseText,
          parseInt(responseData.ok._upgrade_iterations),
          responseData.ok._trustuuid
        )
      } else if (response.response && response.response.error) {
        const error = response.response.error
        if (error._iterations) {
          return wrongIterations(parseInt(error._iterations))
        }
        if (error._cause === 'wrongserver') {
          return wrongServer(error._server)
        }
        if (error._cause === 'outofbandrequired' && error._retryid) {
          return multifactorRetryResponse(error._retryid)
        }
        const challenge = getMultifactorChallenge(requestData.username, error)
        if (challenge) {
          return multifactorChallenge(challenge)
        }
        throw new ServiceError(replaceHTML(error._message))
      }
    }
    throw new UnkownServiceError()
  }
}
