import { Fetch } from '@lastpass/http'
import {
  UnkownServiceError,
  createResponse,
  ResponseTypes,
  ServiceError
} from '../responses'
import { MultifactorChallenge } from '@lastpass/types/multifactor'

export const SUCCESS = 'success'

const success = (otp: string, trustedDeviceId?: string) =>
  createResponse(SUCCESS, { otp, trustedDeviceId })

export const Responses = {
  success
}

export type Responses = ResponseTypes<typeof Responses>

export function create(fetch: Fetch) {
  /* eslint-disable */
  return async function service(
    duoResponse: string,
    challenge: MultifactorChallenge,
    trustedDeviceLabel?: string
  ) {
    if (challenge.provider.duoWebSDK) {
      const [response, status] = await fetch.legacyXML('duo.php', {
        xml: 1,
        sig_response: duoResponse,
        akey: challenge.provider.duoWebSDK.bytes,
        username: challenge.username,
        ...(trustedDeviceLabel && {
          trustlabel: trustedDeviceLabel,
          cansetuuid: 1,
          canexpire: 1
        })
      })
      if (status === 200) {
        if (response) {
          if (response.ok) {
            return success(response.ok._code, response.ok._trustuuid)
          } else if (response.error) {
            throw new ServiceError(response.error)
          }
        }
      }
    }
    throw new UnkownServiceError()
  }
}
