import { Fetch } from '@lastpass/http'
import * as DuoAPI from './duo'
import * as SmsAPI from './sms'
import * as TrustedDeviceAPI from './trusted-device'

export const createMultifactorServices = (fetch: Fetch) => {
  return {
    duo: DuoAPI.create(fetch),
    sms: SmsAPI.create(fetch),
    trustedDevice: TrustedDeviceAPI.create(fetch)
  }
}

export { DuoAPI, SmsAPI, TrustedDeviceAPI }

export type Services = ReturnType<typeof createMultifactorServices>
