import { call, put, spawn } from 'redux-saga/effects'
import * as LoginActions from '@lastpass/state/login/actions'
import * as MultifactorServices from '@lastpass/server/multifactor'
import { IterationsSaga } from './iterations'
import { secretsSaga } from './secrets'
import { LoginServerSaga, LoginServerSagaResult } from './server'
import { createMultifactorSaga } from '../multifactor'
import { MultifactorChallengeResult } from '../multifactor/phases'
import { MultifactorChallengeResponse } from '@lastpass/types/multifactor'
import { TrustedDeviceRepository } from '@lastpass/repositories/multifactor/trusted-device'
import { ServiceError, UnkownServiceError } from '@lastpass/server/responses'
import { LoginData } from '@lastpass/services/login/request'
import { IterationsRepository } from '@lastpass/repositories/login/iterations'

export function createLoginSaga(
  iterationsSaga: IterationsSaga,
  loginServerSaga: LoginServerSaga,
  multifactorServices: MultifactorServices.Services
) {
  return function*(action: ReturnType<typeof LoginActions.login>) {
    try {
      const iterations = yield call(iterationsSaga, action.payload.username)
      const trustedDeviceId = yield call(
        TrustedDeviceRepository.get,
        action.payload.username
      )
      const [vaultKey, loginHash] = yield call(
        secretsSaga,
        action.payload.username,
        action.payload.password,
        iterations
      )
      let loginData: LoginData = {
        username: action.payload.username,
        key: vaultKey,
        hash: loginHash,
        iterations,
        trustedDeviceId,
        authSessionId: action.payload.authSessionId
      }
      let result: LoginServerSagaResult = yield call(
        loginServerSaga,
        action,
        loginData
      )

      // Try again if our cached iterations was wrong
      if (result.iterationsUpdate) {
        yield spawn(
          IterationsRepository.set,
          loginData.username,
          result.iterationsUpdate
        )
        const [vaultKey, loginHash] = yield call(
          secretsSaga,
          action.payload.username,
          action.payload.password,
          result.iterationsUpdate
        )
        loginData = {
          ...loginData,
          key: vaultKey,
          hash: loginHash,
          iterations: result.iterationsUpdate
        }
        result = yield call(loginServerSaga, action, loginData)
      }

      // Start the multifactor phase
      if (result.challenge) {
        yield put(
          LoginActions.multifactorChallenge(
            result.challenge,
            action.payload.username,
            action.payload.password
          )
        )
        const multifactorSaga = createMultifactorSaga(
          multifactorServices,
          function*(multifactorData: MultifactorChallengeResponse) {
            return yield call(
              loginServerSaga,
              action,
              loginData,
              multifactorData
            )
          }
        )
        // TODO We should be able to call `yield call` here but it never returns a value. yield* works so going with that for now.
        const multifactorResult: MultifactorChallengeResult = yield* multifactorSaga(
          result.challenge
          /* eslint-disable-next-line */
        ) as any
        if (multifactorResult.cancelled) {
          yield put(LoginActions.clearMultifactor())
        }
      }
    } catch (e) {
      if (e instanceof ServiceError) {
        yield put(LoginActions.error(e.toString()))
      } else {
        // TODO send to segment
        console.error(e)
        yield put(LoginActions.error(new UnkownServiceError().toString()))
      }
    }
  }
}
