import { sagaMiddleware } from './app-store'
import { Config } from './config'
import { createFetch } from '@lastpass/http'
import { createLoginFeatureSaga } from '@lastpass/sagas/login'
import { createLoginServices } from '@lastpass/server/login'
import { createMultifactorServices } from '@lastpass/server/multifactor'
import { createLoginRequestService } from '@lastpass/services/login/request'
import { extensionLoginSaga } from './sagas/login'

export function initialize(config: Config) {
  if (!config.lastpassUrl) {
    throw new Error('BASE_URL not defined')
  }

  // Services
  const extensionFetch = createFetch(config.lastpassUrl)
  const loginRequestService = createLoginRequestService(config.platform, 2)
  const loginServices = createLoginServices(extensionFetch, loginRequestService)
  const multifactorServices = createMultifactorServices(extensionFetch)

  // Sagas
  sagaMiddleware.run(createLoginFeatureSaga(loginServices, multifactorServices))
  sagaMiddleware.run(extensionLoginSaga)
}
