import { MultifactorState } from './state'
import * as Multifactor from './actions'

const initialState: MultifactorState = {
  sentSMS: false,
  inProcess: false,
  error: ''
}

export function multifactorReducer(
  previousState: MultifactorState | undefined,
  action: Multifactor.Actions
): MultifactorState {
  const state = previousState || initialState
  switch (action.type) {
    case Multifactor.SEND_SMS: {
      return { ...state, inProcess: true }
    }
    case Multifactor.SUBMIT_TOTP: {
      return { ...state, inProcess: true, error: '' }
    }
    case Multifactor.ERROR: {
      return {
        ...state,
        error: action.payload.error,
        inProcess: false
      }
    }
    case Multifactor.CANCEL: {
      return {
        ...state,
        sentSMS: false,
        error: ''
      }
    }
    case Multifactor.SENT_SMS: {
      return {
        ...state,
        sentSMS: true,
        inProcess: false
      }
    }
    case Multifactor.TRUST_DEVICE: {
      return {
        ...state,
        trustedDeviceLabel: action.payload.label
      }
    }
    case Multifactor.UNTRUST_DEVICE: {
      return {
        ...state,
        trustedDeviceLabel: ''
      }
    }
  }
  return state
}
