import { takeEvery, call, put, race, take } from 'redux-saga/effects'
import * as MultifactorActions from '@lastpass/state/multifactor/actions'
import { MultifactorChallenge } from '@lastpass/types/multifactor'
import { OutOfBandSaga } from './outofband'
import { ManualSaga } from './manual'
import { DuoWebSDKSaga } from './duo-web-sdk'
import { SMSSaga } from './sms'
import { MultifactorChallengeResult } from './phases'
import { TrustedDeviceRepository } from '@lastpass/repositories/multifactor/trusted-device'
import { MultifactorSagaState } from './saga-state'

export function createMainSaga(
  smsSaga: SMSSaga,
  duoWebSDKSaga: DuoWebSDKSaga,
  outOfBandSaga: OutOfBandSaga,
  manualSaga: ManualSaga
) {
  const challengeResultSaga = function*(
    challenge: MultifactorChallenge,
    state: MultifactorSagaState
  ) {
    if (challenge.provider.duoWebSDK) {
      return yield call(duoWebSDKSaga, challenge, state)
    } else if (challenge.options.outofband) {
      if (!challenge.options.outofbandauto) {
        const [result, sendPushAction] = yield race([
          call(manualSaga, challenge),
          take(MultifactorActions.SEND_PUSH)
        ])
        if (sendPushAction) {
          return yield call(outOfBandSaga, challenge, state)
        }
        return result
      }
      return yield call(outOfBandSaga, challenge, state)
    } else {
      return yield call(manualSaga, challenge)
    }
  }

  return function*(challenge: MultifactorChallenge) {
    try {
      const state: MultifactorSagaState = {}

      // Keep track of the user entered trusted device label so we can
      // call trust.php once the authentication was successful
      if (challenge.options.outofband) {
        yield takeEvery(
          MultifactorActions.TRUST_DEVICE,
          (action: ReturnType<typeof MultifactorActions.trust>) => {
            state.trustedDeviceLabel = action.payload.label
          }
        )
        yield takeEvery(MultifactorActions.UNTRUST_DEVICE, () => {
          state.trustedDeviceLabel = ''
        })
      }

      yield takeEvery(MultifactorActions.SEND_SMS, smsSaga)

      const result: MultifactorChallengeResult = yield call(
        challengeResultSaga,
        challenge,
        state
      )

      if (result.succeeded) {
        // Save the UUID to disk
        if (result.trustedDeviceId) {
          yield call(
            TrustedDeviceRepository.set,
            challenge.username,
            result.trustedDeviceId
          )
        }
      }

      return result
    } catch (e) {
      yield put(MultifactorActions.error(e.toString()))
    }
  }
}
