import { MultifactorChallengeResponse } from '@lastpass/types/multifactor'
import { SagaIterator } from 'redux-saga'
import { createOutOfBandSaga } from './outofband'
import { createManualSaga } from './manual'
import { createDuoWebSDKSaga } from './duo-web-sdk'
import { createSMSSaga } from './sms'
import { createMainSaga } from './main'
import * as MultifactorServices from '@lastpass/server/multifactor'

export function createMultifactorSaga(
  services: MultifactorServices.Services,
  challengeResponseSaga: (data: MultifactorChallengeResponse) => SagaIterator
) {
  return createMainSaga(
    createSMSSaga(services),
    createDuoWebSDKSaga(services, challengeResponseSaga),
    createOutOfBandSaga(services, challengeResponseSaga),
    createManualSaga(challengeResponseSaga)
  )
}
