import { take, spawn, call, put } from 'redux-saga/effects'
import * as LoginActions from '@lastpass/state/login/actions'
import * as LoginServices from '@lastpass/server/login'
import { LoginData } from '@lastpass/services/login/request'
import { MultifactorChallengeResponse } from '@lastpass/types/multifactor'
import { secretsSaga } from './secrets'
import { pbkdf2IterationMigration } from '@lastpass/services/migrations/encryption/pbkdf2-iterations'
import { IterationsRepository } from '@lastpass/repositories/login/iterations'
import { AddSavedUsernameSaga } from './add-saved-username'

export function createSuccessSaga(addSavedUsernameSaga: AddSavedUsernameSaga) {
  return function* successSaga(
    result: ReturnType<typeof LoginServices.LoginAPI.Responses.success>,
    action: ReturnType<typeof LoginActions.login>,
    data: LoginData,
    multifactorData?: MultifactorChallengeResponse
  ) {
    if (action.payload.rememberUsername) {
      yield spawn(addSavedUsernameSaga, action.payload.username)
    }
    let vaultKey = data.key
    if (result.body.suggestPasswordChange) {
      yield put(LoginActions.suggestChangePassword())
      // Wait for ignore action to continue the rest of the login flow
      yield take(LoginActions.IGNORE_SUGGEST_CHANGE_PASSWORD)
    } else if (result.body.upgradeIterations) {
      try {
        const [key, hash] = yield call(
          secretsSaga,
          data.username,
          action.payload.password,
          result.body.upgradeIterations
        )
        yield call(pbkdf2IterationMigration, {
          username: data.username,
          csrf: result.body.csrf,
          iterations: result.body.upgradeIterations,
          from: data,
          to: {
            key,
            hash
          }
        })
        yield spawn(
          IterationsRepository.set,
          data.username,
          result.body.upgradeIterations
        )
        vaultKey = key
      } catch (e) {}
    }
    yield put(
      LoginActions.success(
        result.body.uid,
        data.username,
        result.body.csrf,
        vaultKey,
        result.body.legacyXML,
        multifactorData && multifactorData.challenge
      )
    )
  }
}

export type SuccessSaga = ReturnType<typeof createSuccessSaga>
