import { parseXml } from './helpers'

type LegacyParam = string | number | boolean
/* eslint-disable-next-line */
type LegacyResponse = any

let csrfToken: string

export function setCSRFToken(token: string) {
  csrfToken = token
}

function isValidParam(param: LegacyParam) {
  const type = typeof param
  return type === 'boolean' || type === 'string' || type === 'number'
}

export function urlEncode(values: { [s: string]: LegacyParam }): string {
  return Object.keys(values).reduce((encoded, key) => {
    if (isValidParam(values[key])) {
      return (
        encoded +
        (encoded ? '&' : '') +
        key +
        '=' +
        encodeURIComponent(values[key].toString())
      )
    }
    return encoded
  }, '')
}

/**
 * Makes a request to an LastPass lmiapi endpoint
 * @param path the path representing the server API to call (defined in sso/lmiapi/config/url_rules.php)
 * @param body the jsonifiable request body
 * @param abortSignal used to cancel the request if necessary
 */
function createFetchLMIAPI(baseUrl: string = '') {
  return async function fetchLMIAPI(
    path: string,
    body?: {},
    abortSignal?: AbortSignal
  ): Promise<[string, number]> {
    const response = await fetch(`${baseUrl}lmiapi/${path}`, {
      signal: abortSignal,
      credentials: 'same-origin',
      ...(body
        ? {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json'
            },
            body: JSON.stringify(body)
          }
        : {})
    })
    let responseBody = ''
    try {
      responseBody = await response.json()
    } catch (e) {}
    return [responseBody, response.status]
  }
}

/**
 * Makes a request to a legacy LastPass endpoint with application/x-www-form-urlencoded content
 * @param url the legacy php endpoint
 * @param body the body to include in the POST response
 */
function createFetchLegacy(baseUrl: string = '') {
  return async function fetchLegacy(
    url: string,
    body?: { [s: string]: LegacyParam },
    abortSignal?: AbortSignal
  ): Promise<[string, number]> {
    const response = await fetch(baseUrl + url, {
      signal: abortSignal,
      credentials: 'same-origin',
      ...(body
        ? {
            method: 'POST',
            headers: {
              'Content-Type': 'application/x-www-form-urlencoded'
            },
            body: urlEncode(csrfToken ? { ...body, token: csrfToken } : body)
          }
        : {})
    })
    const text = await response.text()
    return [text, response.status]
  }
}

function createFetchLegacyXML(baseUrl: string = '') {
  const fetchLegacy = createFetchLegacy(baseUrl)
  return async function fetchLegacyXML(
    url: string,
    body?: { [s: string]: LegacyParam },
    abortSignal?: AbortSignal
  ): Promise<[LegacyResponse, number]> {
    const [xml, status] = await fetchLegacy(url, body, abortSignal)
    const parsed = parseXml(xml)
    return [parsed, status]
  }
}

export function postForm(url: string, body?: { [s: string]: LegacyParam }) {
  const form = document.createElement('form')
  form.method = 'post'
  form.action = url
  if (body) {
    Object.keys(body).forEach(key => {
      if (isValidParam(body[key])) {
        const input = document.createElement('input')
        input.name = key
        input.value = body[key].toString()
        form.appendChild(input)
      }
    })
  }
  document.documentElement.appendChild(form)
  form.submit()
}

export function createFetch(baseUrl: string = '') {
  return {
    legacy: createFetchLegacy(baseUrl),
    legacyXML: createFetchLegacyXML(baseUrl),
    lmiapi: createFetchLMIAPI(baseUrl)
  }
}

export type Fetch = ReturnType<typeof createFetch>
