import localForage from 'localforage'

export const SavedUsernamesRepository = {
  get: async (): Promise<string[]> => {
    const usernames = await localForage.getItem<string[]>('saved_usernames')
    return usernames || []
  },
  set: (usernames: string[]): Promise<string[]> => {
    return localForage.setItem('saved_usernames', usernames)
  }
}
