import localForage from 'localforage'
import { hash } from '@lastpass/encryption/sha'

export const IterationsRepository = {
  get: (username: string): Promise<number | null> => {
    return localForage.getItem(hash(username) + '_iter')
  },
  set: (username: string, iterations: number): Promise<number> => {
    return localForage.setItem(hash(username) + '_iter', iterations)
  }
}
