import { call } from 'redux-saga/effects'
import * as legacy from '@lastpass/services/login/sha'
import * as pbkdf2 from '@lastpass/services/login/pbkdf2'

export function* secretsSaga(
  username: string,
  password: string,
  iterations: number
) {
  if (iterations > 1) {
    const vaultKey = yield call(
      pbkdf2.deriveKey,
      username,
      password,
      iterations
    )
    const loginHash = yield call(pbkdf2.deriveHash, vaultKey, password)
    return [vaultKey, loginHash]
  } else {
    const vaultKey = yield call(legacy.deriveKey, username, password)
    const loginHash = yield call(legacy.deriveHash, vaultKey, password)
    return [vaultKey, loginHash]
  }
}
