import { encrypt } from '@lastpass/encryption/aes/ecb'
import { MultifactorChallengeResponse } from '@lastpass/types/multifactor'
import { Platform } from '@lastpass/types/platform'

export interface LoginData {
  username: string
  key: Buffer
  hash: string
  iterations: number
  trustedDeviceId?: string
  authSessionId?: string
}

export type LoginRequestService = (
  data: LoginData,
  multifactor: MultifactorChallengeResponse | undefined
) => /* eslint-disable-next-line */
Promise<any>

/**
 * Makes request to login.php
 * @param platform the platform
 * @param xmlVersion xml parameter of the login request. 1: web, 2: extension
 */
export function createLoginRequestService(
  platform: Platform,
  xmlVersion: 1 | 2
): LoginRequestService {
  return async (data, multifactor) => {
    const encryptedUsername = await encrypt(data.username, data.key)
    /* eslint-disable-next-line */
    let request: any = {
      hash: data.hash,
      xml: xmlVersion,
      method: platform,
      username: data.username,
      /* eslint-disable-next-line @typescript-eslint/camelcase */
      encrypted_username: encryptedUsername.toString('base64'),
      iterations: data.iterations,
      email: data.username,
      outofbandsupported: 1,
      authSessionId: data.authSessionId,
      uuid: data.trustedDeviceId
    }
    if (multifactor) {
      request.otp = multifactor.totp
      if (multifactor.trustedDeviceLabel) {
        request.trustlabel = multifactor.trustedDeviceLabel
        request.cansetuuid = 1
        request.canexpire = 1
        // Must set this to empty string for server to save trusted device \_()_/
        request.uuid = ''
      }
      // If the user did not submit a TOTP then it must be an outofband polling request
      if (multifactor.challenge && !multifactor.totp) {
        request.outofbandrequest = 1
        request.outofbandretry = multifactor.retryId ? 1 : 0
        request.outofbandretryid = multifactor.retryId
      }
    }
    return request
  }
}
