import { ActionsUnion } from '../types'
import { createAction } from '../action'
import { TrackerFunction } from '@lastpass/middleware/tracking'
import { MultifactorChallenge } from '@lastpass/types/multifactor'

export const CREATEACCOUNT = '[login] create'
export const ONETIMEPASSWORD = '[login] otp'
export const FORGOTPASSWORD = '[login] forgot'
export const REMEMBERUSERNAME = '[login] remember'
export const LOGIN = '[login] login'
export const MFA_CHALLENGE = '[login] mfachallenge'
export const CLEAR_MULTIFACTOR = '[login] clearmfa'
export const ERROR = '[login] error'
export const SUCCESS = '[login] success'
export const INITIALIZE = '[login] initialize'
export const INITIALIZED = '[login] initialized'
export const REMOVEUSERNAME = '[login] removeusername'
export const USERNAMES = '[login] usernames'
export const FEDERATEDLOGIN = '[login] federatedlogin'
export const SUGGEST_CHANGE_PASSWORD = '[login] suggestchangepw'
export const CHANGE_PASSWORD = '[login] changepw'
export const IGNORE_SUGGEST_CHANGE_PASSWORD = '[login] ignoresuggestchangepw'
export const OPEN_TERMS = '[login] terms'
export const OPEN_PRIVACY = '[login] privacy'
export const UPDATE_SERVER_HOST = '[login] updateserver'

/**
 * Initiates a login process
 * @param username cleartext username
 * @param password cleartext password
 */
export const login = (
  username: string,
  password: string,
  rememberUsername: boolean,
  authSessionId?: string
) =>
  createAction(LOGIN, { username, password, rememberUsername, authSessionId })
export const clearMultifactor = () => createAction(CLEAR_MULTIFACTOR)
export const multifactorChallenge = (
  data: MultifactorChallenge,
  email: string,
  password: string
) =>
  createAction(MFA_CHALLENGE, {
    data,
    email,
    password
  })
export const error = (error: string) => createAction(ERROR, { error })
export const success = (
  uid: number,
  username: string,
  csrf: string,
  key: Buffer,
  legacyXml: string,
  challenge?: MultifactorChallenge
) => createAction(SUCCESS, { uid, username, csrf, key, challenge, legacyXml })
export const createAccount = () => createAction(CREATEACCOUNT)
export const oneTimePassword = () => createAction(ONETIMEPASSWORD)
export const forgotPassword = () => createAction(FORGOTPASSWORD)
export const rememberUsername = () => createAction(REMEMBERUSERNAME)
export const setUsernames = (savedUsernames: string[]) =>
  createAction(USERNAMES, { savedUsernames })
export const initialize = () => createAction(INITIALIZE)
export const initialized = () => createAction(INITIALIZED)
export const removeUsername = (username: string) =>
  createAction(REMOVEUSERNAME, { username })
export const federatedLogin = (email: string) =>
  createAction(FEDERATEDLOGIN, { email })
export const changePassword = (username: string) =>
  createAction(CHANGE_PASSWORD, { username })
export const suggestChangePassword = () => createAction(SUGGEST_CHANGE_PASSWORD)
export const ignoreSuggestChangePassword = () =>
  createAction(IGNORE_SUGGEST_CHANGE_PASSWORD)
export const openTerms = () => createAction(OPEN_TERMS)
export const openPrivacy = () => createAction(OPEN_PRIVACY)
export const updateServerHost = (host: string) =>
  createAction(UPDATE_SERVER_HOST, { host })

export const Actions = {
  login,
  multifactorChallenge,
  clearMultifactor,
  error,
  success,
  createAccount,
  oneTimePassword,
  forgotPassword,
  rememberUsername,
  setUsernames,
  initialize,
  initialized,
  removeUsername,
  federatedLogin,
  changePassword,
  suggestChangePassword,
  ignoreSuggestChangePassword,
  openTerms,
  openPrivacy,
  updateServerHost
}

export type Actions = ActionsUnion<typeof Actions>

const actionType = (action: Actions) => action.type
export type ActionTypes = ReturnType<typeof actionType>
export type Tracker = [ActionTypes, TrackerFunction<Actions>]
