import localForage from 'localforage'
import { hash } from '@lastpass/encryption/sha'

export const TrustedDeviceRepository = {
  get: (username: string): Promise<string> => {
    return localForage.getItem(hash(username) + '_trust')
  },
  set: (username: string, id: string): Promise<string> => {
    return localForage.setItem(hash(username) + '_trust', id)
  }
}
