import { pbkdf2 } from '@lastpass/encryption/pbkdf2'

/**
 * Generates both the vault encryption key
 * @param username
 * @param password
 * @param iterations
 */
export function deriveKey(
  username: string,
  password: string,
  iterations: number
): Promise<Buffer> {
  return pbkdf2(password, username, iterations, 32, 'sha256')
}
