export interface MultifactorRequestResult {
  succeeded: boolean
  retryId?: string
  trustedDeviceId?: string
}

export interface MultifactorChallengeResult {
  cancelled: boolean
  succeeded: boolean
  error?: string
  trustedDeviceId?: string
  trustedDeviceLabel?: string
}

export function cancel(): MultifactorChallengeResult {
  return {
    cancelled: true,
    succeeded: false
  }
}

export function success(
  trustedDeviceId?: string,
  trustedDeviceLabel?: string
): MultifactorChallengeResult {
  return {
    cancelled: false,
    succeeded: true,
    trustedDeviceId,
    trustedDeviceLabel
  }
}

export function error(message: string) {
  return {
    cancelled: false,
    succeeded: false,
    error: message
  }
}
