interface Secrets {
  key: Buffer
  hash: string
}

interface PBKDF2IterationsMigrationData {
  username: string
  csrf: string
  iterations: number
  from: Secrets
  to: Secrets
}

export function pbkdf2IterationMigration(
  data: PBKDF2IterationsMigrationData
): Promise<void> {
  return new Promise((resolve, reject) => {
    const changer = new window['PWCHANGER']()
    changer.hashMigration(
      data.from.key.toString('binary'),
      data.from.hash,
      data.username,
      data.to.key.toString('binary'),
      data.to.hash,
      '',
      data.csrf,
      data.iterations,
      function(success, errors) {
        if (!success) {
          reject('PBKDF2 Iteration Migration Failed: ' + (errors || ''))
        } else {
          resolve()
        }
      }
    )
  })
}
