import { takeEvery, put, call } from 'redux-saga/effects'
import * as LoginActions from '@lastpass/state/login/actions'
import * as MultifactorActions from '@lastpass/state/multifactor/actions'
import { MultifactorProvider } from '@lastpass/types/multifactor'
import { urlEncode } from '@lastpass/http'

/* eslint-disable-next-line */
const bg = window as any

function openURL(url: string) {
  bg.openURL(bg.base_url + url)
}

function loginSuccess(action: ReturnType<typeof LoginActions.success>) {
  bg.finishReactLogin(
    action.payload.uid,
    action.payload.username,
    action.payload.key.toString('binary'),
    action.payload.csrf,
    action.payload.legacyXml
  )
}

export function isMultifactorProviderSupported(
  provider: MultifactorProvider
): boolean {
  switch (provider) {
    case MultifactorProvider.SESAME:
    case MultifactorProvider.GRID:
    case MultifactorProvider.FINGERPRINT:
      return false
    default:
      return true
  }
}

function twoFactorFallback(
  action: ReturnType<typeof LoginActions.multifactorChallenge>
) {
  if (!isMultifactorProviderSupported(action.payload.data.provider.type)) {
    bg.LP_do_login(action.payload.email, action.payload.password)
  }
}

function isFederated(username: string): Promise<boolean> {
  return new Promise(resolve => {
    bg.FederatedLogin.isFederated(username, resolve)
  })
}

function getPassword(username: string): Promise<[string, string]> {
  return new Promise(resolve => {
    bg.FederatedLogin.getPassword(username, resolve)
  })
}

function* federatedLogin(
  action: ReturnType<typeof LoginActions.federatedLogin>
) {
  const isFederatedUser: boolean = yield call(isFederated, action.payload.email)

  if (isFederatedUser) {
    const [password, authSessionId] = yield call(
      getPassword,
      action.payload.email
    )
    yield put(
      LoginActions.login(action.payload.email, password, false, authSessionId)
    )
  }
}

function disableMultifactor(
  action: ReturnType<typeof MultifactorActions.disable>
) {
  openURL(
    `/disablemultifactor.php?${urlEncode({
      cmd: 'sendemail',
      username: action.payload.challenge.username,
      multifactortype: action.payload.challenge.provider.type
    })}`
  )
}

function changePassword(
  action: ReturnType<typeof LoginActions.changePassword>
) {
  openURL(
    `/passwordreset.php?${urlEncode({
      fromprompt: 1,
      lpnorefresh: 1,
      u: action.payload.username
    })}`
  )
}

export function* extensionLoginSaga() {
  yield takeEvery(LoginActions.CREATEACCOUNT, () => {
    // TODO show extension create account
  })
  yield takeEvery(LoginActions.ONETIMEPASSWORD, () => {
    openURL('otp.php?forcelogin=1')
  })
  yield takeEvery(LoginActions.FORGOTPASSWORD, () => {
    openURL('forgot.php')
  })
  yield takeEvery(LoginActions.OPEN_TERMS, () => {
    openURL('terms.php')
  })
  yield takeEvery(LoginActions.OPEN_PRIVACY, () => {
    openURL('privacy.php')
  })
  yield takeEvery(LoginActions.SUCCESS, loginSuccess)
  yield takeEvery(LoginActions.MFA_CHALLENGE, twoFactorFallback)
  yield takeEvery(MultifactorActions.DISABLE, disableMultifactor)
  yield takeEvery(LoginActions.CHANGE_PASSWORD, changePassword)
  yield takeEvery(LoginActions.FEDERATEDLOGIN, federatedLogin)
}
