import { call, put } from 'redux-saga/effects'
import * as LoginActions from '@lastpass/state/login/actions'
import { SavedUsernamesRepository } from '@lastpass/repositories/login/saved-usernames'

export function* initializeLoginSaga() {
  const savedUsernames = yield call(SavedUsernamesRepository.get)
  yield put(LoginActions.setUsernames(savedUsernames))
  yield put(LoginActions.initialized())
}
