import { call, race, take } from 'redux-saga/effects'
import * as MultifactorActions from '@lastpass/state/multifactor/actions'
import DuoWebSDK from 'duo_web_sdk'
import {
  MultifactorChallenge,
  MultifactorChallengeResponse
} from '@lastpass/types/multifactor'
import { SagaIterator } from 'redux-saga'
import * as MultifactorServices from '@lastpass/server/multifactor'
import { error, cancel, success } from './phases'
import { UnkownServiceError } from '@lastpass/server/responses'
import { MultifactorSagaState } from './saga-state'

/* eslint-disable */
export function getDUOResponse(
  challenge: MultifactorChallenge
): Promise<string> {
  return new Promise(resolve => {
    if (challenge.provider.duoWebSDK) {
      DuoWebSDK.init({
        host: challenge.provider.duoWebSDK.host,
        sig_request: challenge.provider.duoWebSDK.signature,
        submit_callback: (form: HTMLFormElement) => {
          resolve(form.sig_response.value)
        }
      })
    } else {
      throw new UnkownServiceError()
    }
  })
}

export function createDuoWebSDKSaga(
  services: MultifactorServices.Services,
  requestSaga: (data: MultifactorChallengeResponse) => SagaIterator
) {
  return function*(
    challenge: MultifactorChallenge,
    state: MultifactorSagaState = {}
  ) {
    try {
      if (challenge.provider.duoWebSDK) {
        const [duoResponse, cancelled] = yield race([
          call(getDUOResponse, challenge),
          take(MultifactorActions.CANCEL)
        ])
        if (cancelled) {
          return cancel()
        } else {
          const response: MultifactorServices.DuoAPI.Responses = yield call(
            services.duo,
            duoResponse,
            challenge,
            state.trustedDeviceLabel
          )
          if (response.type === MultifactorServices.DuoAPI.SUCCESS) {
            yield call(requestSaga, {
              challenge,
              totp: 'checkduo' + response.body.otp
            })
            return success(
              response.body.trustedDeviceId,
              state.trustedDeviceLabel
            )
          }
        }
      }
    } catch (e) {
      return error(e.toString())
    }
  }
}

export type DuoWebSDKSaga = ReturnType<typeof createDuoWebSDKSaga>
