import { LoginState } from './state'
import * as Login from './actions'

const initialState: LoginState = {
  inProcess: false,
  username: '',
  error: '',
  savedUsernames: [],
  suggestPasswordChange: false,
  initialized: false
}

export function loginReducer(
  previousState: LoginState | undefined,
  action: Login.Actions
): LoginState {
  const state = previousState || initialState
  switch (action.type) {
    case Login.INITIALIZED: {
      return {
        ...state,
        initialized: true
      }
    }
    case Login.MFA_CHALLENGE: {
      return {
        ...state,
        multifactor: action.payload.data,
        inProcess: false
      }
    }
    case Login.CLEAR_MULTIFACTOR: {
      return {
        ...state,
        multifactor: undefined
      }
    }
    case Login.USERNAMES: {
      return {
        ...state,
        savedUsernames: action.payload.savedUsernames
      }
    }
    case Login.SUGGEST_CHANGE_PASSWORD: {
      return {
        ...state,
        suggestPasswordChange: true
      }
    }
    case Login.LOGIN: {
      return {
        ...state,
        inProcess: true,
        username: action.payload.username,
        error: ''
      }
    }
    case Login.SUCCESS: {
      return {
        ...state,
        inProcess: false,
        username: ''
      }
    }
    case Login.ERROR: {
      return {
        ...state,
        inProcess: false,
        username: '',
        error: action.payload.error
      }
    }
  }
  return state
}
