import { pbkdf2 as _pbkdf2 } from 'pbkdf2'

export function pbkdf2(
  password: string | Buffer,
  salt: string,
  iterations: number,
  keylen: number,
  digest: string
): Promise<Buffer> {
  return new Promise((resolve, reject) => {
    try {
      _pbkdf2(password, salt, iterations, keylen, digest, (error, key) => {
        if (error) {
          reject(error)
        } else {
          resolve(key)
        }
      })
    } catch (e) {
      reject(e)
    }
  })
}
