import { ActionsUnion } from '../types'
import { createAction } from '../action'
import { TrackerFunction } from '@lastpass/middleware/tracking'
import { SMSData, MultifactorChallenge } from '@lastpass/types/multifactor'

export const SEND_SMS = '[multifactor] sms'
export const SENT_SMS = '[multifactor] sentsms'
export const SUBMIT_TOTP = '[multifactor] totp'
export const TRUST_DEVICE = '[multifactor] trust'
export const UNTRUST_DEVICE = '[multifactor] untrust'
export const CANCEL = '[multifactor] cancel'
export const ERROR = '[multifactor] error'
export const DISABLE = '[multifactor] disable'
export const SEND_PUSH = '[multifactor] sendpush'

/**
 * Sends an SMS to the users phone as a backup multifactor method
 */
export const sms = (data: SMSData) => createAction(SEND_SMS, { data })

export const sentSMS = () => createAction(SENT_SMS)

/**
 * This should be dispatched when both the trust device input is checked and
 * there is a label is populated. It should be dispatched every time the label changes
 * and debounced so we don't generate an action for every keystroke
 *
 * @param label the user defined label of the trusted device
 */
export const trust = (label: string) => createAction(TRUST_DEVICE, { label })

/**
 * Should be called if the user unchecks the trust device input
 */
export const untrust = () => createAction(UNTRUST_DEVICE)

export const submit = (totp: string, label: string) =>
  createAction(SUBMIT_TOTP, { totp, label })

export const cancel = () => createAction(CANCEL)

export const disable = (challenge: MultifactorChallenge) =>
  createAction(DISABLE, { challenge })

export const sendPush = () => createAction(SEND_PUSH)

export const error = (error: string) => createAction(ERROR, { error })

export const Actions = {
  sms,
  sentSMS,
  trust,
  untrust,
  submit,
  disable,
  error,
  cancel,
  sendPush
}

export type Actions = ActionsUnion<typeof Actions>

const actionType = (action: Actions) => action.type
export type ActionTypes = ReturnType<typeof actionType>
export type Tracker = [ActionTypes, TrackerFunction<Actions>]
