import { spawn, call, put, cancelled } from 'redux-saga/effects'
import * as LoginActions from '@lastpass/state/login/actions'
import * as LoginServices from '@lastpass/server/login'
import { LoginData } from '@lastpass/services/login/request'
import {
  MultifactorChallengeResponse,
  MultifactorChallenge
} from '@lastpass/types/multifactor'
import { MultifactorRequestResult } from '../multifactor/phases'
import { SuccessSaga } from './success'
import { setCSRFToken } from '@lastpass/http/fetch'

export interface LoginServerSagaResult extends MultifactorRequestResult {
  iterationsUpdate?: number
  challenge?: MultifactorChallenge
}

export function createLoginServerSaga(
  services: LoginServices.Services,
  successSaga: SuccessSaga
) {
  return function*(
    action: ReturnType<typeof LoginActions.login>,
    data: LoginData,
    challengeResponse?: MultifactorChallengeResponse
  ) {
    const abortController = new AbortController()
    try {
      const result: LoginServices.LoginAPI.Responses = yield call(
        services.login,
        data,
        challengeResponse,
        abortController.signal
      )
      switch (result.type) {
        case LoginServices.LoginAPI.SUCCESS: {
          yield call(setCSRFToken, result.body.csrf)
          yield spawn(successSaga, result, action, data, challengeResponse)
          const sagaResult: LoginServerSagaResult = {
            succeeded: true,
            trustedDeviceId: result.body.trustedDeviceId
          }
          return sagaResult
        }
        case LoginServices.LoginAPI.REQUIREPWCHANGE: {
          yield put(LoginActions.changePassword(action.payload.username))
          const sagaResult: LoginServerSagaResult = { succeeded: false }
          return sagaResult
        }
        case LoginServices.LoginAPI.WRONG_SERVER: {
          yield put(LoginActions.updateServerHost(result.body.host))
          const sagaResult: LoginServerSagaResult = { succeeded: false }
          return sagaResult
        }
        case LoginServices.LoginAPI.WRONG_ITERATIONS: {
          const sagaResult: LoginServerSagaResult = {
            succeeded: false,
            iterationsUpdate: result.body.iterations
          }
          return sagaResult
        }
        case LoginServices.LoginAPI.MULTIFACTOR_CHALLENGE: {
          const sagaResult: LoginServerSagaResult = {
            succeeded: false,
            challenge: result.body
          }
          return sagaResult
        }
        case LoginServices.LoginAPI.MULTIFACTOR_RETRY_RESPONSE: {
          const sagaResult: LoginServerSagaResult = {
            succeeded: false,
            retryId: result.body.retryId
          }
          return sagaResult
        }
      }
    } finally {
      if (yield cancelled()) {
        abortController.abort()
      }
    }
  }
}

export type LoginServerSaga = ReturnType<typeof createLoginServerSaga>
