import { call } from 'redux-saga/effects'
import { IterationsRepository } from '@lastpass/repositories/login/iterations'
import * as LoginServices from '@lastpass/server/login'

export function createIterationsSaga(services: LoginServices.Services) {
  return function*(username: string) {
    const iterations = yield call(IterationsRepository.get, username)
    if (!iterations) {
      const result: LoginServices.IterationsAPI.Responses = yield call(
        services.iterations,
        username
      )
      if (result.type === LoginServices.IterationsAPI.SUCCESS) {
        yield call(IterationsRepository.set, username, result.body)
        return result.body
      }
    }
    return iterations
  }
}

export type IterationsSaga = ReturnType<typeof createIterationsSaga>
