export enum MultifactorProvider {
  DUO = 'duo',
  GOOGLE_AUTHENTICATOR = 'googleauth',
  GRID = 'grid',
  LASTPASS_AUTHENTICATOR = 'lastpassauth',
  LASTPASS_MFA = 'lastpassmfa',
  FINGERPRINT = 'multifactor',
  MICROSOFT_AUTHENTICATOR = 'microsoftauth',
  SALESFORCE = 'salesforcehash',
  SECURE_AUTH = 'secureauth',
  SECURID = 'securid',
  SESAME = 'sesame',
  SYMANTEC_VIP = 'symantecvip',
  TOOPHER = 'toopher',
  TRANSAKT = 'transakt',
  YUBIKEY = 'yubikey'
}

export interface MultifactorOptions {
  outofband: boolean
  outofbandauto: boolean
  verificationCode: boolean
  disable: boolean
  trust: boolean
}

export interface SMSData {
  uid: string
  hash: string
  time: number
  nextCode?: string
}

export interface DUOWebSDK {
  host: string
  bytes: string
  signature: string
}

export interface MultifactorChallenge {
  username: string
  provider: {
    type: MultifactorProvider
    /**
     * Legacy method of showing outofband provider icons
     */
    image?: string
    /**
     * Legacy method of showing outofband provider label
     */
    label?: string

    duoWebSDK?: DUOWebSDK
  }
  options: MultifactorOptions
  /**
   * Whether or not the trust period (30 days) has expired. Determined by the server.
   */
  trustExpired: boolean
  /**
   * If trust has expired, this was the label of the device that the client previously trusted. This saves the user the step of
   * typing it in again if they want to retrust.
   */
  trustedDeviceLabel?: string
  /**
   * An option returned by login.php. Will support for now
   */
  textOverride?: string
  /**
   * Data required to send a totp via sms instead of using the multifactor application
   */
  sms?: SMSData
}

export interface MultifactorChallengeResponse {
  challenge: MultifactorChallenge
  /**
   * If the multifactor is happening "out of band" we poll lastpass until it tells us the multifactor was succesful
   */
  retryId?: string
  /**
   * The TOTP code entered by the user generated by their multifactor application
   */
  totp?: string
  /**
   * The user entered label of the device they want to trust (temporarily disable multifactor). Only used when
   * manually submitting a TOTP code
   */
  trustedDeviceLabel?: string
}
