import { call, put } from 'redux-saga/effects'
import * as MultifactorActions from '@lastpass/state/multifactor/actions'
import * as MultifactorServices from '../../server/multifactor'

export function createSMSSaga(services: MultifactorServices.Services) {
  return function* saga(action: ReturnType<typeof MultifactorActions.sms>) {
    const result: MultifactorServices.SmsAPI.Responses = yield call(
      services.sms,
      action.payload.data
    )
    if (result.type === MultifactorServices.SmsAPI.SUCCESS) {
      yield put(MultifactorActions.sentSMS())
    }
  }
}

export type SMSSaga = ReturnType<typeof createSMSSaga>
